import React, { useState } from 'react';
import { useSpring, a } from '@react-spring/web';
import { useNavigate } from 'react-router-dom';
import styles from './styles.module.css';

export default function App() {
  const navigate = useNavigate();

  // Datos de las tarjetas
  const cards = [
    { id: 1, title: 'Videojuegos', subtitle: 'Códigos \n Guías \n Recomendaciones', image: '/images/card1.png', route: '/code', icon: '/images/icon1.png' },
    // { id: 2, title: 'Ciberseguridad', subtitle: 'Códigos \n Guías \n Recomendaciones', image: '/images/card2.jpg', route: '/cyber', icon: '/images/icon3.png' },
    // { id: 3, title: 'Tarjeta 3', subtitle: 'Ir a Página 3', image: '/images/onDie3.png', route: '/page3' },
  ];

  return (
    <div className={styles.pageContainer}>
      <h1 className={styles.title}>Shuxyzz</h1>
      <div className={styles.container}>
        {cards.map((card) => (
          <Card key={card.id} card={card} navigate={navigate} />
        ))}
      </div>
    </div>
  );
}

// Componente de Tarjeta
function Card({ card, navigate }) {
  const [hovered, setHovered] = useState(false);

  const scale = useSpring({
    transform: hovered ? 'scale(1.1)' : 'scale(1)',
    config: { tension: 300, friction: 20 },
  });

  return (
    <a.div
      className={styles.card}
      style={{
        ...scale,
        backgroundColor: hovered ? 'transparent' : 'black',
      }}
      onMouseEnter={() => setHovered(true)}
      onMouseLeave={() => setHovered(false)}
      onClick={() => navigate(card.route)}
    >
      <div
        className={styles.image}
        style={{
          backgroundImage: hovered ? `url(${card.image})` : 'none',
        }}
      />
      <div className={styles.textContainer}>
        <img src={card.icon} alt="Icono" className={styles.icon} />
        <h2>{card.title}</h2>
        <p className={styles.subtitle}>{card.subtitle}</p>
      </div>
    </a.div>
  );
}